// ============================================
// LOWORLD - Bento Grid Design System
// ============================================

:host {
  display: block;
  height: 100%;
  overflow-y: auto;
  background-color: #f5f9fc;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
}

.page-container {
  max-width: 1000px; // Constrain width for better readability like the image
  margin: 0 auto;
  padding: 80px 20px 100px; // Top padding for fixed nav
}

// Typography
h1, h2, h3, h4, p { margin: 0; }

.section-label {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 1.2rem;
  font-weight: 700;
  color: #1d1d1f;
  margin: 40px 0 20px;
  
  .highlight {
    color: #007aff;
  }
}

// Grid System
.bento-grid-3 {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  
  @media (max-width: 768px) {
    grid-template-columns: 1fr;
  }
}

.bento-grid-2 {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;

  @media (max-width: 768px) {
    grid-template-columns: 1fr;
  }
}

.bento-grid-4 {
  display: grid;
  grid-template-columns: repeat(2, 1fr); // 2x2 layout
  gap: 20px;

  @media (max-width: 768px) {
    grid-template-columns: 1fr;
  }
}

// Card Base
.bento-card {
  background: #ffffff;
  border-radius: 24px;
  padding: 24px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.04);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  border: 1px solid rgba(0,0,0,0.03);

  &:hover {
    transform: translateY(-4px);
    box-shadow: 0 12px 30px rgba(0, 0, 0, 0.08);
  }
}

// Specific Card Styles

// 1. Header Card (Blue)
.header-card {
  background: linear-gradient(135deg, #e0f2fe 0%, #bae6fd 100%);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px;
  margin-bottom: 20px;

  .header-content {
    display: flex;
    align-items: center;
    gap: 16px;
    
    .icon-box {
      width: 48px;
      height: 48px;
      background: #0ea5e9;
      border-radius: 12px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: white;
      font-size: 24px;
    }

    .text-box {
      h1 {
        font-size: 1.5rem;
        font-weight: 800;
        color: #0f172a;
      }
      p {
        font-size: 0.95rem;
        color: #334155;
        margin-top: 4px;
      }
    }
  }

  .badge {
    background: #ff5252;
    color: white;
    padding: 6px 16px;
    border-radius: 20px;
    font-weight: 700;
    font-size: 0.9rem;
  }
  
  @media (max-width: 768px) {
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
  }
}

// 2. Quote Card (Light Blue)
.quote-card {
  background: #eef2ff; // Very light indigo/blue
  text-align: center;
  padding: 40px;
  margin-bottom: 40px;
  border: 1px solid #e0e7ff;

  .quote-text {
    font-size: 1.4rem;
    font-weight: 700;
    color: #374151;
    line-height: 1.5;
    margin-bottom: 20px;
    font-style: italic;
  }

  .quote-sub {
    font-size: 0.95rem;
    color: #6b7280;
    line-height: 1.6;
    
    .highlight {
      color: #4f46e5;
      font-weight: 600;
    }
  }
}

// 3. Info Cards (Icon + Title + Desc)
.info-card {
  display: flex;
  flex-direction: column;
  gap: 16px;

  .card-icon {
    font-size: 32px;
    margin-bottom: 8px;
  }

  h3 {
    font-size: 1.1rem;
    font-weight: 700;
    color: #111827;
  }

  p {
    font-size: 0.95rem;
    color: #4b5563;
    line-height: 1.6;
  }
}

// 4. Value Cards (With specific colored badges/icons)
.value-card {
  .tag {
    display: inline-block;
    padding: 4px 12px;
    border-radius: 12px;
    font-size: 0.8rem;
    font-weight: 700;
    margin-bottom: 12px;
    
    &.red { background: #fee2e2; color: #ef4444; }
    &.blue { background: #dbeafe; color: #2563eb; }
    &.green { background: #dcfce7; color: #16a34a; }
  }
}

// 5. Operation Cards (Horizontal layout for some)
.operation-card {
  display: flex;
  gap: 16px;
  align-items: flex-start;

  .op-icon {
    width: 40px;
    height: 40px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    flex-shrink: 0;
    
    &.bg-blue { background: #e0f2fe; color: #0284c7; }
    &.bg-red { background: #fee2e2; color: #dc2626; }
    &.bg-yellow { background: #fef3c7; color: #d97706; }
    &.bg-green { background: #dcfce7; color: #16a34a; }
  }

  .op-content {
    h4 {
      font-size: 1rem;
      font-weight: 700;
      margin-bottom: 6px;
    }
    p {
      font-size: 0.9rem;
      color: #6b7280;
      line-height: 1.5;
    }
    .sub-tag {
      display: inline-block;
      margin-top: 8px;
      font-size: 0.8rem;
      color: #0ea5e9;
      font-weight: 600;
      background: #f0f9ff;
      padding: 2px 8px;
      border-radius: 4px;
    }
  }
}

// 6. Provider Pills (Small horizontal cards)
.provider-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 16px;
  
  @media (max-width: 768px) {
    grid-template-columns: 1fr;
  }
}

.provider-pill {
  background: #ffffff;
  border-radius: 16px;
  padding: 16px;
  display: flex;
  align-items: center;
  gap: 12px;
  border: 1px solid #f3f4f6;
  
  .pill-icon {
    font-size: 20px;
  }
  
  span {
    font-weight: 600;
    font-size: 0.95rem;
    color: #374151;
  }
}

// 7. Target Audience Cards (Blue background)
.target-card {
  background: #f0f9ff; // Light blue bg
  text-align: center;
  padding: 30px 20px;
  
  .target-icon {
    font-size: 40px;
    margin-bottom: 16px;
  }
  
  h4 {
    font-size: 1.1rem;
    font-weight: 700;
    margin-bottom: 8px;
    color: #1e293b;
  }
  
  p {
    font-size: 0.9rem;
    color: #64748b;
    line-height: 1.5;
  }
}

// 8. Vision Card (Dark)
.vision-card {
  background: #1f2937; // Dark grey
  color: white;
  text-align: center;
  padding: 60px 20px;
  margin-top: 60px;
  border-radius: 24px;
  
  .vision-icon {
    font-size: 48px;
    margin-bottom: 20px;
    color: #fbbf24; // Gold star
  }
  
  h2 {
    font-size: 1.8rem;
    font-weight: 700;
    margin-bottom: 20px;
  }
  
  p {
    font-size: 1.1rem;
    color: #9ca3af;
    line-height: 1.8;
    max-width: 600px;
    margin: 0 auto;
    
    .highlight {
      color: #60a5fa; // Blue highlight
      font-weight: 600;
    }
  }
}

// 9. Curriculum Card (White, simple)
.curriculum-card {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-top: 20px;
  
  .rocket-icon {
    font-size: 40px;
  }
  
  .curr-content {
    h3 { font-size: 1.2rem; font-weight: 700; margin-bottom: 4px; }
    p { color: #6b7280; }
  }
}

// Navigation (Keep existing but simplified)
.nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 60px;
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(12px);
  z-index: 100;
  border-bottom: 1px solid rgba(0,0,0,0.05);
  display: flex;
  align-items: center;
  padding: 0 20px;
  
  .nav-container {
    width: 100%;
    max-width: 1000px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .logo { font-weight: 800; font-size: 1.2rem; color: #000; text-decoration: none; }
  .nav-cta { 
    background: #000; color: white; padding: 8px 16px; border-radius: 20px; 
    font-size: 0.9rem; font-weight: 600; cursor: pointer;
  }
}

.back-button {
  display: flex;
  align-items: center;
  gap: 4px;
  font-size: 1rem;
  font-weight: 600;
  color: #1d1d1f;
  cursor: pointer;
  transition: opacity 0.2s;
  
  &:hover {
    opacity: 0.7;
  }
  
  svg {
    margin-top: -1px;
  }
}
